"use client";

import React, { useState } from "react";
import dynamic from "next/dynamic";
import { MapPin, Loader2 } from "lucide-react";
import { Card, CardContent, CardHeader, CardTitle } from "@/app/components/ui/card";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/app/components/ui/select";

// Define types
type WaterQualityRecord = {
  quality: string;
  details: string;
};

type WaterQualityData = {
  [location: string]: WaterQualityRecord;
};

type Location = {
  lat: number;
  lng: number;
};

type LocationsData = {
  [location: string]: Location;
};

// Static data
const locations: LocationsData = {
  Alibag: { lat: 18.6411, lng: 72.8722 },
  Panvel: { lat: 18.9894, lng: 73.1175 },
  Pune: { lat: 18.5204, lng: 73.8567 },
  Mumbai: { lat: 19.0760, lng: 72.8777 },
};

const waterQualityData: WaterQualityData = {
  Alibag: { quality: "Good", details: "pH: 7.5, Turbidity: Low" },
  Panvel: { quality: "Fair", details: "pH: 6.9, Turbidity: Medium" },
  Pune: { quality: "Excellent", details: "pH: 7.2, Turbidity: Very Low" },
  Mumbai: { quality: "Poor", details: "pH: 6.4, Turbidity: High" },
};

// Dynamically import Leaflet components with proper types
const MapContainer = dynamic(
  () => import("react-leaflet").then((mod) => mod.MapContainer),
  { ssr: false }
);

const TileLayer = dynamic(
  () => import("react-leaflet").then((mod) => mod.TileLayer),
  { ssr: false }
);

const Marker = dynamic(
  () => import("react-leaflet").then((mod) => mod.Marker),
  { ssr: false }
);

// Main Component
const WaterQualityMap = () => {
  const [selectedLocation, setSelectedLocation] = useState<string | null>(null);
  const [loading, setLoading] = useState(false);
  const [result, setResult] = useState<WaterQualityRecord | null>(null);

  const handleLocationChange = async (location: string) => {
    setLoading(true);
    setSelectedLocation(location);

    // Simulate API call
    await new Promise((resolve) => setTimeout(resolve, 1000));
    setResult(waterQualityData[location]);
    setLoading(false);
  };

  const getQualityColor = (quality: string): string => {
    switch (quality) {
      case "Excellent":
        return "text-green-600";
      case "Good":
        return "text-blue-600";
      case "Fair":
        return "text-yellow-600";
      case "Poor":
        return "text-red-600";
      default:
        return "text-gray-600";
    }
  };

  // Convert location to [lat, lng] array for Leaflet
  const getLocationCoords = (locationName: string): [number, number] => {
    const location = locations[locationName];
    return [location.lat, location.lng];
  };

  return (
    <div className="min-h-screen bg-gray-50 p-8">
      <Card className="max-w-4xl mx-auto">
        <CardHeader>
          <CardTitle className="text-center text-2xl font-bold">
            Water Quality Map Analysis
          </CardTitle>
        </CardHeader>
        <CardContent className="space-y-6">
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
            <div className="space-y-4">
              <Select onValueChange={handleLocationChange}>
                <SelectTrigger>
                  <SelectValue placeholder="Select a location" />
                </SelectTrigger>
                <SelectContent>
                  {Object.keys(locations).map((location) => (
                    <SelectItem key={location} value={location}>
                      {location}
                    </SelectItem>
                  ))}
                </SelectContent>
              </Select>

              <div className="h-[400px] w-full rounded-lg overflow-hidden">
                {typeof window !== "undefined" && (
                  <MapContainer
                    center={[18.5204, 73.8567]} // Default center (Pune)
                    zoom={8}
                    className="h-full w-full"
                  >
                    <TileLayer
                      url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
                      attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
                    />
                    {Object.keys(locations).map((locationName) => (
                      <Marker
                        key={locationName}
                        position={getLocationCoords(locationName)}
                      />
                    ))}
                  </MapContainer>
                )}
              </div>
              <p className="text-sm text-gray-500 text-center">
                Select a location from the dropdown to view its water quality.
              </p>
            </div>

            <div className="bg-white rounded-lg p-6 border h-fit">
              {loading ? (
                <div className="flex items-center justify-center h-full">
                  <Loader2 className="w-8 h-8 animate-spin text-blue-500" />
                </div>
              ) : selectedLocation && result ? (
                <div className="space-y-4">
                  <div className="flex items-center gap-2">
                    <MapPin className="w-5 h-5 text-blue-500" />
                    <h3 className="text-lg font-semibold">{selectedLocation}</h3>
                  </div>

                  <div className="space-y-2">
                    <p className="text-gray-600">Water Quality:</p>
                    <p className={`text-2xl font-bold ${getQualityColor(result.quality)}`}>
                      {result.quality}
                    </p>
                  </div>

                  <div className="pt-4 border-t">
                    <h4 className="font-medium mb-2">Detailed Analysis:</h4>
                    <p className="text-gray-600">{result.details}</p>
                  </div>
                </div>
              ) : (
                <div className="text-center text-gray-500">
                  <MapPin className="w-12 h-12 mx-auto mb-3 opacity-50" />
                  <p>Select a location to view water quality data</p>
                </div>
              )}
            </div>
          </div>
        </CardContent>
      </Card>
    </div>
  );
};

export default WaterQualityMap;